
// List
// ––––––––––––––––––––––––––––––––––––––––––––––––––

dl,
ol,
ul
    list-style: none
    margin-top: 0
    padding-left: 1.1em

    dl,
    ol,
    ul
        font-size: 90%
        margin: calc(1.5rem * .625) 0 calc(1.5rem * .625) calc(3.0rem * .625)

ol
    list-style: decimal outside

ul
    list-style: circle outside
